.modalHeading {
  font-family: "BARTKEY";
  font-size: 50px;
  color: white;
  margin-bottom: 40px;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-between;
  align-items: center;
}
#submitBtn {
  /* background: red; */
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-wrap: nowrap;
  flex-direction: row;
  align-content: center;
}
#submit-form {
  color: black;
  font-size: 18px;
  /* background: white; */
  font-family: "Montserrat";
  border: none;
  padding: 5px 30px;
  font-weight: bold;
  transition: all 0.7s;
}
#submit-form:hover {
  cursor: pointer;
  opacity: 0.6;
}

