.genderContainer {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: space-between;
  align-items: center;
  width: 95%;
}

span {
  padding: 10px;
}

.genderContainerInput {
  width: 300px;
  display: flex;
  justify-content: space-between;
}

.registerBtn {
  color: black;
  font-size: 18px;
  /* background: white; */
  font-family: "Montserrat";
  padding: 5px 30px;
  font-weight: bold;
  transition: all 0.7s;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
}
.registerBtn:hover {
  cursor: pointer;
  opacity: 0.6;
}
.registerBtnBorder {
  border: 1px solid white;
  padding: 3px;
}

.register-lp {
  display: block;
}

.register-mobile {
  display: none;
}

.signDate {
  z-index: 9999;
  position: relative;
  color: white;
  top: -110px;
  left: -10px;
  font-weight: bold;
  text-align: left;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  align-content: center;
  justify-content: center;
  align-items: center;
  font-family: Montserrat;
  font-size: 1em;
}

#drawer {
  height: 100%;
  width: 25%;
  position: fixed;
  z-index: 100;
  top: 0;
  right: 0;
  overflow-x: hidden;
  transition: 0.5s;
  text-align: center;
  padding-top: 100px;
  background: rgba(0, 0, 0, 0.85);
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateX(100%);
}
#drawer a {
  width: 60%;
  padding: 20px;
  font-weight: 600;
  font-family: "Montserrat";
  color: white;
  background: rgba(25, 25, 27, 0.09);
  mix-blend-mode: normal;
  box-shadow: 0px 5px 2px rgba(255, 255, 255, 0.02),
    inset 0px -106.879px 88.6312px -83.4176px rgba(16, 12, 24, 0.05),
    inset 0px 5.2136px 23.4612px rgba(94, 87, 147, 0.15),
    inset 0px 1.3034px 52.136px rgba(161, 155, 199, 0.05);
  /* filter: blur(100px); */
  /* Note: backdrop-filter has minimal browser support */
  border-radius: 10px;
  margin: 20px 0;
  text-decoration: none;
}

@media screen and (max-width: 768px) {
  .registerBtnWrapper {
    position: absolute;
    left: 1.5rem;
  }
  /* 
  #registerTop{
    position: absolute;
    bottom: calc((100vw * 285) / 393 - 10rem);
    right: 1rem;
    z-index: 60;
  } */
  .modalHeading {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-content: center;
    justify-content: space-between;
    align-items: center;
    font-size: 36px;
    margin-left: 20px;
  }
  .register-mobile {
    display: block;
  }
}

